header {
  max-width: 1308px;
  margin: 0 auto;
  width: 100%;
  height: 663px;
  background-image: url("../images/parralax-header.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding-top: 270px;
  color: #FFFFFF; }
  header h1 {
    font-size: 6em;
    font-family: "Open Sans";
    font-style: normal;
    font-variant: normal;
    font-weight: 400;
    text-transform: uppercase;
    padding-bottom: 35px; }
    header h1 span {
      font-weight: 600; }
  header hr {
    margin: 0 auto;
    width: 14.5em;
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid #da491c; }
  header p {
    padding-top: 40px;
    font-size: 1.6em;
    line-height: 2.2em; }

@media screen and (max-width: 740px) {
  header {
    padding: 220px 20px 0 20px; }
    header h1 {
      font-size: 4.6em; }
    header p {
      font-size: 1.4em; }
    header br {
      display: none; } }

nav {
  max-width: 1308px;
  margin: 0 auto;
  left: 0px;
  right: 0px;
  top: 0;
  z-index: 999;
  position: fixed;
  width: 100%;
  height: 60px;
  color: #FFFFFF;
  background-color: rgba(0, 0, 0, 0.7);
  animation: opacity 2s; }

@keyframes opacity {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  nav .logo {
    width: 50%;
    float: left;
    padding: 20px 70px; }
    nav .logo img {
      display: inline-block; }
  nav .menu {
    width: 50%;
    float: left; }

nav:before,
nav:after {
  content: "";
  display: table;
  clear: both; }

@media screen and (max-width: 740px) {
  nav {
    height: 120px; }
    nav .logo {
      width: 100%;
      clear: both;
      text-align: center; }
    nav .menu {
      width: 100%;
      clear: both; } }

section {
  max-width: 1308px;
  margin: 0 auto;
  display: block;
  clear: both; }
  section .main-text {
    width: 44%;
    padding: 75px 65px;
    display: inline-block; }
    section .main-text .verticle-line {
      border-left: 2px solid #da491c;
      height: 46px;
      display: inline-block;
      float: left;
      margin-right: 20px; }
    section .main-text h2 {
      font-size: 3.6em;
      font-family: "Open Sans", serif;
      font-style: normal;
      font-variant: normal;
      font-weight: 400;
      text-transform: uppercase;
      padding-bottom: 35px; }
    section .main-text p:nth-child(3) {
      font-family: 'Merriweather', serif;
      font-weight: 100;
      font-size: 1.6em;
      line-height: 2em;
      color: #5c5c5c;
      margin-bottom: 45px; }
    section .main-text p {
      font-family: "Open Sans", serif;
      font-size: 14px;
      color: #333333;
      line-height: 1.8em;
      padding-bottom: 25px; }
  section .services {
    width: 56%;
    display: inline-block;
    float: right; }
    section .services .flex-services {
      display: flex; }
      section .services .flex-services .service {
        flex: 1;
        border-style: solid;
        border-width: 1px;
        border-color: #cccccc;
        color: 333333;
        cursor: pointer;
        padding-top: 95px;
        transition: padding 0.5s ease;
        position: relative;
        height: 290px; }
        section .services .flex-services .service i {
          margin: 0 auto; }
        section .services .flex-services .service p {
          padding: 0 20px; }
        section .services .flex-services .service p:nth-child(2) {
          margin-top: 35px;
          font-size: 14px;
          font-weight: 600;
          text-transform: uppercase;
          text-align: center; }
        section .services .flex-services .service p:nth-child(3) {
          margin-top: 20px;
          font-size: 13px;
          text-align: center;
          line-height: 20px;
          display: none;
          color: #999999; }
        section .services .flex-services .service .more-info {
          background-color: #da491c;
          background-image: url("../images/more-info.png");
          background-repeat: no-repeat;
          background-position: center;
          position: absolute;
          bottom: 0px;
          width: 100%;
          padding: 25px;
          text-align: center;
          display: none; }
      section .services .flex-services .service:hover {
        padding-top: 40px;
        transition: padding 0.5s ease;
        background-color: #292929; }
        section .services .flex-services .service:hover p:nth-child(2) {
          color: #FFF;
          transition: color 0.3s ease; }
        section .services .flex-services .service:hover p:nth-child(3) {
          display: block; }
        section .services .flex-services .service:hover .more-info {
          display: block; }

section::after {
  content: "";
  clear: both;
  display: block; }

@media screen and (max-width: 1024px) {
  section .main-text {
    padding: 35px 25px;
    width: 100%; }
  section .services {
    width: 100%;
    display: block; }
    section .services .flex-services {
      display: block; }
      section .services .flex-services .service {
        display: block;
        margin: 0 auto;
        width: 80%; } }

